<template>
 <div>
   <div class="row form-group">    
        <div class='col-lg-12' style='display:flex;flex-direction:row'>
          <label style='padding-right:0.5em;padding-top:0.2em'>Class</label>
            <select name="Request_for" id="f" class="form-control select" v-model="locker.locker_form" @change="updatelist()" style='width:5em'>
                  <option v-for="fl in formlevel">{{ fl }}</option>
            </select>
            <select name="Request_for" id="cls" class="form-control select" v-model="locker.locker_class" @change="updatelist()" style='width:5em'>
                  <option v-for="fcls in formclass">{{ fcls }}</option>
            </select>
            <div style='margin-left:auto;display:flex;flex-direction:row'>
            <label style='padding-right:0.5em;padding-top:0.2em'>Status</label>
            <select name="Status" id="cls" class="form-control select" v-model="locker.locker_status" @change="updatelist()" style='width:10em;'>
                  <option v-for="stat in status">{{ stat }}</option>
            </select>
            </div>
        </div>       
    </div>
    <div data-app class="table-responsive">
    <v-data-table
      :headers="headers"
      :items="pooldata"
      :search="search"
      class="elevation-1"
      :rows-per-page-items="[20]"
    >
    <template v-slot:items="props">
      <td><a href="" :id= props.item.id v-on:click.prevent="editform($event)" style='font-size:1.3rem'><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a></td>
      <td style='width:5em'>{{ props.item.form }}</td>
      <td>{{ props.item.class }}</td>
      <td>{{ props.item.lockerid}}</td>
      <td style='width:40em'>{{ props.item.description }}</td>
      <td>{{ props.item.action}}</td>
      <td>{{ props.item.create_name }}</td>
      <td style='width:5em'>{{ props.item.status}}</td>
    
      <template v-slot:pageText="props">
        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
      </template>
    
    </template>
    </v-data-table>
</div>
</div>
</template>

<script>
import axios from 'axios';

export default {
    data(){
        return{
           search: '',
            pooldata : [],
            userid:"",
            formlevel:['F1','F2','F3','F4','F5'],  
            formclass:['*','A','B','C','D','E'],
            status:['Pending','Completed'],
            locker:{
              locker_form:'F1',
              locker_class:'*',
              locker_status:'Pending',
            },
        headers: [
          { text: 'Action',value :'id',sortable: false},
          { text: 'Form',value:'form',sortable: false},
          { text: 'Class',value:'class',sortable: false},
          { text: 'Locker ID',value:'lockerid',sortable: false},
          { text: 'Description',value:'description',sortable: false},
          { text: 'Follow Up Action',value:'action',sortable: false},
          { text: 'Submitted By',value:'create_name',sortable: false},
          { text: 'Status',value:'status',sortable: false}
        ],
        }        
    },
    async created(){
        var formData = new FormData();
        formData.append('Form', this.locker.locker_form);
        formData.append('Class', this.locker.locker_class);
        formData.append('Status', this.locker.locker_status);
        await axios.post('RequestPortal/backend/lockerlist.php',formData)
        .then(
            response=>{
                this.pooldata = response.data
            }
        );   
    },
    methods:{
        editform(event){
            var targetID = event.currentTarget.id;
            document.getElementById('tmp_id').value = targetID;
            this.$router.push('edit_locker');
        },
        updatelist(){
          var formData = new FormData();
          formData.append('Form', this.locker.locker_form);
          formData.append('Class', this.locker.locker_class);
          formData.append('Status', this.locker.locker_status);
          axios.post('RequestPortal/backend/lockerlist.php',formData)
          .then(
            response=>{
                this.pooldata = response.data
            }
          ); 
        }
    }
}
</script>




